*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{height:100vh;background-color:#45a3e5;-webkit-animation:14s ease 0s infinite alternate none running home-background;animation:14s ease 0s infinite alternate none running home-background}@-webkit-keyframes home-background{25%{background:#41c4de}50%{background:#8cc73f}75%{background:#f3d333}100%{background:#f78e56}}@keyframes home-background{25%{background:#41c4de}50%{background:#8cc73f}75%{background:#f3d333}100%{background:#f78e56}}.logo{color:#fff;font-family:"Acme",sans-serif;font-size:8rem;margin:0}.title{border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.title .join{background-color:white;z-index:3;margin:50px 50px 0px 50px;padding:30px;font-weight:900}.title .pin{background-color:white;z-index:3;padding:30px;margin:0px 50px 50px 50px;font-size:8rem;font-weight:900}.players-count{font-family:'Montserrat', sans-serif;font-size:4rem;font-weight:bold;color:white}.names{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1.6rem;font-family:'Montserrat', sans-serif;font-weight:bold;padding:20px 50px;font-size:2.4rem;color:white}.start-btn{font-size:1.6rem;min-height:4.8rem}.start-btn span{margin:0 20px;font-weight:bold}.music{top:0;right:0;position:fixed}

.title{font-family:'Montserrat', sans-serif;font-size:1.8rem;font-weight:800}.middle{font-family:'Montserrat', sans-serif;font-size:3rem;font-weight:800;color:white;background:#f78e56}.questions{font-size:2.4rem}.footer{font-size:1.25rem;padding:16px;font-family:'Montserrat', sans-serif}

.question-block{background:#41c4de}.question-tracker{background:rgba(0,0,0,0.2);color:white;font-size:3rem;top:0;left:0;position:fixed;padding:20px}.main-question{font-family:'Montserrat', sans-serif;line-height:1.7;font-size:2rem;background:#fff;padding:10px;text-align:center;font-weight:bold}

.top-question{font-family:'Montserrat', sans-serif;line-height:1.6;font-size:0.8rem;padding:10px;text-align:center;font-weight:bold;border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.controls{background:#f2f2f2;padding:10px}.host-answers{font-size:1.2rem;font-weight:bold;color:white;background:#f2f2f2;border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.answer-count{font-family:'Montserrat', sans-serif;font-size:1.7rem;font-weight:bold;color:#000}.time{font-family:'Montserrat', sans-serif;font-size:1.7rem;font-weight:bold;color:#fff;padding:1.5rem;border-radius:100%;background-color:#8a4af3}

.home{height:100vh;background-color:#45a3e5;-webkit-animation:14s ease 0s infinite alternate none running home-background;animation:14s ease 0s infinite alternate none running home-background}.main-title{color:#fff;font-family:"Acme",sans-serif;font-size:100px;padding-bottom:1rem;margin:0}@-webkit-keyframes home-background{25%{background:#41c4de}50%{background:#8cc73f}75%{background:#f3d333}100%{background:#f78e56}}@keyframes home-background{25%{background:#41c4de}50%{background:#8cc73f}75%{background:#f3d333}100%{background:#f78e56}}span{margin:0 auto !important}p{font-size:1.6rem}

.top-info{font-size:1.25rem;padding:16px;font-family:'Montserrat', sans-serif;font-weight:800;border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.question-number{font-family:'Montserrat', sans-serif;font-size:3rem;font-weight:800;color:white;background:#f3d333}span{margin-right:30px}

.answers-top-info{font-size:1.25rem;padding:16px;font-family:'Montserrat', sans-serif;font-weight:800;border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.answer-block{background:white}

.info{font-size:1.25rem;padding:16px;font-family:'Montserrat', sans-serif;font-weight:800;border-bottom:0.1875rem solid rgba(0,0,0,0.15)}.main-info{background-color:#66bf39;color:#fff;font-family:'Montserrat', sans-serif;font-weight:800}.in{font-size:2.5rem;font-family:'Rubik', sans-serif;font-style:italic}.name{font-size:1.25rem}

